.checkoutProduct {
  display: flex;
  margin: 20px 0;
}
.checkoutProduct__info {
  padding: 0 0 0 20px;
}
.checkoutProduct__info > button {
  background: #f0c14b;
  border: 1px solid;
  margin: 10px 0 0 0;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}
.checkoutProduct__image {
  object-fit: contain;
  width: 180px;
  height: 180px;
}
.checkoutProduct__rating {
  display: flex;
}
.checkoutProduct__title {
  font-size: 17px;
  font-weight: 800;
}

